import React from 'react';
import { Row, Col, Form, Select } from 'antd';
import ReactEchartsCore from 'echarts-for-react/lib/core';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/pie';
import CityTable from './CityTable';
import {
  STATISTIC_PERIOD_ADD,
  STATISTIC_BEFORE_PERIOD_REMAIN,
  STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_THIS_YEAR_TOTAL,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN,
  STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL,
  STATISTIC_BEFORE_PERIOD_NEED_HANDLE,
  STATISTIC_BEFORE_PERIOD_TOTAL,
  STATISTIC_PERIOD_RATE
} from '../constants';

class CityView extends React.Component {
  static defaultProps = {
    cities: []
  }

  constructor(props) {
    super(props);
    this.state = {
      filteredCities: []
    }
  }

  filterCities = (filteredCities) => {
    if (filteredCities != null && filteredCities.length > 0) {
      this.setState({
        filteredCities: filteredCities
      });
    } else {
      this.setState({
        filteredCities: []
      });
    }
  }

  render() {
    return (
      <div>
        <Row>
          <Col span={12}>
            <ReactEchartsCore
              echarts={echarts}
              option={{
                title: {
                  text: '剩余未关城市分布',
                  textStyle: {
                    color: '#000000'
                  }
                },
                tooltip : {
                  trigger: 'item',
                  formatter: '{b}:{c}({d}%)'
                },
                series: [
                  {
                    name: '城市',
                    type: 'pie',
                    label: {
                      formatter: '{b}:{c}({d}%)'
                    },
                    data: this.props.cityOpenData
                  }
                ]
              }}
            />
          </Col>
          <Col span={12}>
            <ReactEchartsCore
              echarts={echarts}
              option={{
                title: {
                  text: '剩余未关分类分布',
                  textStyle: {
                    color: '#000000'
                  }
                },
                tooltip : {
                  trigger: 'item',
                  formatter: '{b}:{c}({d}%)'
                },
                series: [
                  {
                    name: '二级分类',
                    type: 'pie',
                    label: {
                      formatter: '{b}:{c}({d}%)'
                    },
                    data: this.props.secondCategoryOpenData
                  }
                ]
              }}
            />
          </Col>
        </Row>
        <Form>
          <Row>
            <Col span={16}>
              <Form.Item
                label='请选择要过滤的城市'
                labelCol={{ span: 4 }}
                wrapperCol={{ span: 16 }}
              >
                <Select
                  mode="multiple"
                  style={{ width: '100%' }}
                  onChange={this.filterCities}
                >
                  {
                    this.props.cities.map((city) => (
                      <Select.Option key={city}>{city}</Select.Option>
                    ))
                  }
                </Select>
              </Form.Item>
            </Col>
          </Row>
        </Form>
        <Row>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_PERIOD_ADD}
              data={this.props.periodNewAddedData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN}
              data={this.props.thisYearBeforePeriodData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_BEFORE_PERIOD_REMAIN_TOTAL}
              data={this.props.thisYearBeforePeriodTotalData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_THIS_YEAR_TOTAL}
              data={this.props.thisYearTotalData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN}
              data={this.props.lastYearBeforePeriodData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_LAST_YEAR_BEFORE_PERIOD_REMAIN_TOTAL}
              data={this.props.lastYearBeforePeriodTotalData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_BEFORE_PERIOD_NEED_HANDLE}
              data={this.props.beforePeriodNeedHandleData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_BEFORE_PERIOD_TOTAL}
              data={this.props.periodTotalData}
              filteredCities={this.state.filteredCities}
            />
          </div>
          <div style={{ margin: '20px 0' }}>
            <CityTable
              title={STATISTIC_PERIOD_RATE}
              data={this.props.rateData}
              filteredCities={this.state.filteredCities}
            />
          </div>
        </Row>
      </div>
    );
  }
}

export default CityView;
